import React from 'react';
import ListCard from './ListCard';
import nextId from "react-id-generator";

const List = (result)=>{
    //console.log(result.result);
    
    const searchlist = result.result.map(res=>{
        const htmlId = nextId()
       return <ListCard key={htmlId} value={res}/>
        
    // only asign key to the root element in a list of elements
        });
    

return <div >{searchlist}</div>
};

export default List;